html, body {
    background-color: white;
    font-family: 'Varela Round', sans-serif;
}

.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    transition: .5s ease;
    opacity: 0.5;
  }
  
  .overlay:hover {
    opacity: 0.8;
  }

.tab-content>.active {
    display: flex;
}

.op75 {
    opacity: 0.75;
}

.op50 {
    opacity: 0.5;
}

.op25 {
    opacity: 0.25;
}

.custom-card {
    min-width: 500px;
}